import React from "react";

import { useFirebaseNode } from "../hooks/useFirebaseNode";

import ScatterQuotesApexChart from "./ScatterQuotesApexChart";
import DatalineQuotesApexChart from "./DatalineQuotesApexChart";

import Image from "react-bootstrap/Image";

export default function ShowMedia(props) {
    const { data: mediaItems, loading, error } = useFirebaseNode(
      props.datafirenode
    );
    //  console.log(props.content, mediaItems);
  
    if (!!loading || !mediaItems) {
      return <div>...</div>;
    }
    if (!!error) {
      return <div>:-)</div>;
    }
    
  //  console.log(mediaItems);

    return (
      <div>
        {Object.keys(mediaItems).map((mediaItem, mediaItemsIndex) => {
          if (
            mediaItem === "scatterquotesapexchart" ||
            mediaItem === "scatterquotesapexchartoptions"
          ) { return <GetReadyScatterQuotesApexChart key={mediaItemsIndex} firenode={mediaItems[mediaItem]} /> }
          if (
            mediaItem === "datalinequotesapexchart" ||
            mediaItem === "datalinequotesapexchartoptions"
          ) { return <GetReadyDatalineQuotesApexChart key={mediaItemsIndex} firenode={mediaItems[mediaItem]} /> }
  
          if (mediaItem === "images") { return <ShowImages key={mediaItemsIndex} urls={mediaItems.images.urls} /> }
  
          return null;
        })}
      </div>
    );
  }

    
  function GetReadyScatterQuotesApexChart({ firenode }) {
    let series = Object.keys(firenode.series).map(
      (scatter) => {
        return [
          firenode["series"][scatter][0],
          firenode["series"][scatter][1],
        ];
      }
    );
    //       console.log(series)
    return <ScatterQuotesApexChart series={series} options={
      !!firenode?.options
        ? firenode.options
        : {}
    } />
  }
  
  
  
  function GetReadyDatalineQuotesApexChart({ firenode }) {
    let series = Object.keys(firenode.series).map((line) => {
      return {
        name: firenode["series"][line]["name"],
        data: Object.keys(
          firenode["series"][line]["data"]
        ).map((line_point) => {
          return [
            firenode["series"][line]["data"][line_point][0],
            firenode["series"][line]["data"][line_point][1],
          ];
        }),
      };
    });
 //    console.log(series);
    return <DatalineQuotesApexChart
      series={series}
      options={
        !!firenode?.options
          ? firenode.options
          : {}
      }
    />
  }
  
  function ShowImages({ urls }) {
    return <>
      {urls?.[0] ? <Image src={urls[0]} /> : null}
      {urls?.[1] ? <Image src={urls[1]} /> : null}
      {urls?.[2] ? <Image src={urls[2]} /> : null}
      {urls?.[3] ? <Image src={urls[3]} /> : null}
      {urls?.[4] ? <Image src={urls[4]} /> : null}
    </>
  }
  