import React from "react";
import ReactHtmlParser from 'react-html-parser'; //, { processNodes, convertNodeToElement, htmlparser2 }

import ShowMedia from "../mediatemplates/ShowMedia";
import { useFirebaseNode } from "../hooks/useFirebaseNode";

import Layout from "../components/layout"
import Button from 'react-bootstrap/Button'
import Collapse from 'react-bootstrap/Collapse'
import Card from 'react-bootstrap/Card'
import Badge from 'react-bootstrap/Badge'

export default function OpenMediaByTheme() {
    const { data: usersCraft, loading, error } = useFirebaseNode('openmedia');

    if (!!loading || !usersCraft) { return <Layout><div>...</div></Layout> }

    if (!!error) { return <Layout><div>:-(</div></Layout> }

    let allposts = [];
    Object.keys(usersCraft).forEach(openmedia_key => { allposts.push(usersCraft[openmedia_key]) });

    //  console.log(allposts);

    const uniqueThemes = !!allposts ? [...new Set(allposts.map(item => item.theme))] : []; // [ 'A', 'B']

    // console.log(uniqueThemes);

    return <Layout>
        {uniqueThemes.map((theme, index) => <ResultsByTheme key={theme} theme={theme} index={index} theme_posts={allposts.filter(item => item.theme === theme)} />)}
    </Layout>
}

function ResultsByTheme(props) {
    const [open, setOpen] = React.useState(false);

    const uniqueTitles = !!props.theme_posts ? [...new Set(props.theme_posts.map(item => item.title))] : [];
    return (
        <Card className="m-3">
            <Card.Header>{props.theme}</Card.Header>
            <Card.Body>
                <Button
                    onClick={() => setOpen(!open)}
                    aria-controls={"collapse-theme" + props.index}
                    aria-expanded={open}
                    variant="outline-secondary"
                    className="m-2"
                >
                    {props.theme} <Badge variant="secondary">{props.theme_posts.length}</Badge>
                </Button>
                <Collapse in={open}>
                    <div id={"collapse-theme" + props.index}>
                        {uniqueTitles.map((title, index) => <ResultsByTitle index={index} title={title} title_posts={props.theme_posts.filter(item => item.title === title)} />)}
                    </div>
                </Collapse>
            </Card.Body>
        </Card>

    );
}

function ResultsByTitle(props) {
    const [openTitle, setOpenTitle] = React.useState(false);

    return <div key={props.title}>
        <Button
            onClick={() => setOpenTitle(!openTitle)}
            aria-controls={"collapse-theme" + props.index}
            aria-expanded={openTitle}
            variant="outline-secondary"
            className="m-1"
        >
            {props.title} <Badge variant="secondary">{props.title_posts.length}</Badge>
        </Button>

        <Collapse in={openTitle}>
            <div id={"collapse-theme" + props.index}>
                {props.title_posts.map(post => <CollapsablePost post={post} />)}
            </div>
        </Collapse>
    </div>
}

function CollapsablePost(props) {
    const [open, setOpen] = React.useState(false);

    return <Card className="mt-1">
        <Card.Header>{props.post.user}</Card.Header>
        <Card.Body>
            <Button
                onClick={() => setOpen(!open)}
                aria-controls={"collapse-post" + props.post.id}
                aria-expanded={open}
                variant="outline-secondary"
            >
                {open ? 'Свернуть' : 'Раскрыть'}
            </Button>
            <Collapse in={open}>
                <div id={"collapse-post" + props.post.id}>
                    {!!props.post?.htmlfeedback ? ReactHtmlParser(props.post.htmlfeedback) : props.post?.comment}
                    {props.post.type === "media" ? (
                        <ShowMedia datafirenode={"/openmediadata/" + props.post.content} />) : null}
                </div>
            </Collapse>
        </Card.Body>
    </Card>

}